import { generateTypes, generateActions, generateSelectors } from "../util";

export const key = "map";

// fetch map info
export const FETCH_MAP_INFO = generateTypes(key, "info");
export const [
  fetchMapInfo,
  fetchMapInfoSuccess,
  fetchMapInfoError,
  fetchMapInfoBusy
] = generateActions(FETCH_MAP_INFO);
export const [
  selectMapInfo,
  selectMapInfoError,
  selectMapInfoBusy,
  selectMapInfoRequest
] = generateSelectors(FETCH_MAP_INFO);
// fetch map style
export const FETCH_MAP_STYLE = generateTypes(key, "style");
export const [
  fetchMapStyle,
  fetchMapStyleSuccess,
  fetchMapStyleError,
  fetchMapStyleBusy
] = generateActions(FETCH_MAP_STYLE);
export const [
  selectMapStyle,
  selectMapStyleError,
  selectMapStyleBusy,
  selectMapStyleRequest
] = generateSelectors(FETCH_MAP_STYLE);
